
#db {
  font-family: Helvetica Neue, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0 0 1rem;
  padding: 0;
}
fieldset {
  margin: 0 0 1rem;
  padding: 1rem;
}
label,
input {
  display: block;
}
input {
  font-size: 1.25rem;
  padding: 0.5rem 1rem;
}
label {
  margin: 0 0 1rem;
}
[class*="grid__col--"] {
  margin-right: 1rem;
}
@media (min-width: 625px) {
.grid {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    width: 100%;
}
.grid__col--3 {
    width: 25%;
}
.grid__col--6 {
    width: 50%;
}
.grid__col--9 {
    width: 75%;
}
[class*="grid__col--"]:last-child,
  [class*="grid__col--"]:only-child {
    margin-right: 0;
}
}



