
.table__row:hover .action {
  opacity: 1;
}
.action {
  opacity: 0;
  display: inline-block;
  padding: 0 0.5rem;
  margin: 0;
  cursor: pointer;
  -webkit-transition: color 0.2s, opacity 0.2s;
  transition: color 0.2s, opacity 0.2s;
}
.action::-moz-selection {
  background: none;
}
.action::selection {
  background: none;
}
.action--edit {
  font-size: 0.9rem;
  font-weight: normal;
  color: CadetBlue;
}
.action--edit:hover {
    color: Teal;
}
.action--remove {
  margin-top: 3px;
  color: red;
}
.action--remove:hover {
    color: darkred;
}



